*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
@mixin flex($dir, $align) {
    display: flex;
    justify-content: center;
    align-items: $align;
    flex-direction: $dir;
}
@mixin dimension {
    width: 50%;
    height: 95vh;
}

@mixin mobileSameProp {
    width: 100vw;
    height: 100vh;
    border-radius: none;
    margin: 0;
    box-shadow: none;
}

.container{
    @include flex(row, center);
    width: 100vw;
    height: 100vh;
    background-color: rgb(231, 231, 231);
    .container__left{
        @include flex(column, center);
        @include dimension;
        margin-left: 17px;
        margin-right: 2px;
        border-radius: 5px 0 0 5px;
        background-color: rgb(247, 247, 247);
        img{
            // border: 2px solid green;
            width: 100%;
            height: 75%;
        }
        .container__left--content{
            @include flex(column, center);
            position: relative;
            top: -60px;
            p{
                width: 55%;
                margin-top: 5px;
                text-align: center;
            }
        }
    }
    .container__right{
        @include flex(column, center);
        @include dimension;
        border-radius: 0 5px 5px 0;
        margin-right: 17px;
        margin-left: 2px;
        background-color: white;
        box-shadow: 5px 5px 5px rgb(193, 193, 193);
        form{
            @include flex(column, left);
            height: 80vh;
            width: 50vw;
            padding: 0 80px 0 80px;
            h1{
                margin-bottom: 50px;
            }
            p{
                color: red;
                font-size: 11px;
                margin-bottom: 10px;
            }
            label{
                // margin: 0;
                color: gray;
                margin-bottom: 8px;
                padding: 0;
            }
            input{
                height: 60px;
                width: 70%;
                border-radius: 5px;
                border: 1px solid lightgray;
            }
            .form__phoneno{
                width: 32%;
            }
            .form__tc{
                margin: 5px 0 10px 0;
                input{
                    width: auto;
                    height: auto;
                    margin-right: 10px;
                    cursor: pointer;
                }
                label{
                    color: black;
                }
            }
            button{
                cursor: pointer;
                color: white;
                letter-spacing: 1px;
                border-radius: 5px;
                border: 1px solid steelblue;
                background-color: steelblue;
                height: 70px;
                width: 40%;
                &:disabled{
                    cursor: not-allowed;
                }
            }
        }
    }
}
.container__barchart{
    background-color: rgb(227, 227, 227);
}
.svg{
    height: 90vh;
    // width: 90vw;
    // border: 2px solid;
    box-shadow: 5px 5px 5px lightgrey, -5px -5px 5px  lightgray;
    // margin-left: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.bar {
    fill: rgb(119, 119, 119);
}
@media only screen and (max-width: 768px){
    .container{
        @include flex(column, center);
        width: 100vw;
        height: auto;
        .container__left{
            @include mobileSameProp;
        }
        .container__right{
            @include mobileSameProp;
            form{
                @include flex(column, left);
                height: 100vh;
                width: 100vw;
                padding: 0 0 0 30px;
            }
        }
    }
}